
body {
    position: relative;
}

.Modal {
    position: fixed;
    z-index: 500;
    background-color: white;
    width: 70%;
    border: 1px solid #ccc;
    box-shadow: 1px 1px 1px black;
    padding: 16px;
    left: 15%;
    top: 30%;
    box-sizing: border-box;
    -webkit-transition: all 0.3s ease-out;
    transition: all 0.3s ease-out;
}

.modalDialog {
    position: fixed;
    font-family: Arial, Helvetica, sans-serif;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: rgba(0,0,0,0.8);
    z-index: 99999;
    -webkit-transition: opacity 400ms ease-in;
    transition: opacity 400ms ease-in;
    ##display: none;
    ##pointer-events: none;
}

.modalDialog:target {
    display: block;
    ##pointer-events: auto;
}

.modalDialog > div {
    width: 400px;
    position: relative;
    margin: 10% auto;
    padding: 5px 20px 13px 20px;
    border-radius: 10px;
    background: #fff;
    background: -o-linear-gradient(#fff, #999);
    z-index: 999999;
}

.close {
    background: #606061;
    color: #fff;
    line-height: 25px;
    position: absolute;
    right: -12px;
    text-align: center;
    top: -10px;
    width: 24px;
    text-decoration: none;
    font-weight: bold;
    border-radius: 12px;
    box-shadow: 3px 3px 3px #fd6;
    z-index: 999999;
}

.close:hover { 
    background: #00d9ff;
    cursor: pointer;
 }

@media (min-width: 600px) {
    .Modal {
        width: 500px;
        left: calc(50% - 250px);
    }
}
ul {
  list-style: none;
}
.pagination {
  display: flex;
}

.pagination > li > button,
.pagination > li > span {
  position: relative;

  padding: 1px 12px;
  margin-left: -1px;
  line-height: 1.42857143;
  color: #337ab7;
  text-decoration: none;
  background-color: #fff;
  border: 1px solid #ddd;
}

.pagination > .active > button,
.pagination > .active > button:focus,
.pagination > .active > button:hover,
.pagination > .active > span,
.pagination > .active > span:focus,
.pagination > .active > span:hover {
  z-index: 3;
  color: #fff;
  cursor: pointer;
  background-color: #337ab7;
  border-color: #337ab7;
}

.is-invalid {
  border: 3px solid red;
}

.invalid-feedback {
  color: red;
}

#success-alert {
  display: none;
}

#myModal {
  display: block;
}

